// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "_colorStyles";
@import "base/nullstyle";
@import "base/wrapper";

.title{
  font-family: var(--font-title);
  color: $black;
  font-size: 32px;
  line-height: 39px;
  @media screen and (max-width: 600px){
    font-size: 24px;
    line-height: 30px;
  }
}



[class*="__container"] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}


body {
  &._lock {
    overflow: hidden;
  }
  font-family: var(--font-main);
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: $black;
  background: $white;
}

// подключения компонентов страницы
@import "general-block/header";
@import "page2/_casino";
@import "page7/_best";