@charset "UTF-8";
:root {
  --font-main: "Popins-reg", sans-serif;
  --font-title: "Popins-semb", sans-serif;
  --green-light: #BBFF85;
  --white: #FFFFFF;
  --black: #0E0C14;
  --black2: #0D0F20;
  --header-input-bg: rgba(154, 94, 245, 0.15);
  --white-opacity: rgba(255, 255, 255, 0.60);
  --purple: #9A5EF5;
  --purple2: #2C0064;
  --purple3: #6202FE;
  --purple-dark: #13012A;
  --purple-grad: linear-gradient(270deg, rgba(72, 51, 107, 0.3) 0%, rgba(40, 29, 59, 0.3) 100%);
  --purple-grad-hover: linear-gradient(270deg, rgba(72, 51, 107, 0.5) 0%, rgba(40, 29, 59, 0.5) 100%);
  --blue-shade: #243A64;
  --blue-gray: #C1C1D4;
  --gray: #8E8E98;
  --red: #C0281C;
}

