}
.casino__breadcrumbs-link:not(:last-child) {
  margin: 0 18px 0 0;
}
.casino__breadcrumbs-link:not(:last-child)::before {
  content: "•";
  position: absolute;
  font-size: 12px;
  line-height: 20px;
  color: #D2E4DD;
